.videoContainer{
    display: inline-block;
    margin-right: 10px;
    position: relative;
    border: 2px solid red;
    margin-left: 35px;
}
.close{
    position:absolute;
    background-color: white;
    right: 0;
   
}

.update{
    position:absolute;
    background-color: white;
    right: 3em;
}